 */
*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #27363b;
  font-family: "Noe Text", sans-serif;
  font-weight: normal;
  line-height: 1.7;
  margin: 0;
  text-rendering: optimizeLegibility;
}

.row {
  margin-top: 20px;
  margin-bottom: 20px;
}

@media only screen and (min-width: 56.25em) {
  .row {
    margin-top: 40px;
    margin-bottom: 40px;
  }
}
img, iframe, video:not(.o-background_video) {
  max-width: 100%;
}

img.cms-plugin {
  box-shadow: inset 0 1px 1px 0 rgba(255, 255, 255, 0.1), 0 50px 100px -20px rgba(50, 50, 93, 0.25), 0 30px 60px -30px rgba(0, 0, 0, 0.3);
}

.page-content {
  padding: 50px 20px;
}

@media only screen and (min-width: 56.25em) {
  .pt200 {
    padding-top: 200px;
  }
}
.section {
  padding: 40px 0;
}
@media only screen and (min-width: 56.25em) {
  .section {
    padding: 80px 0;
  }
}

.colored-bkg {
  background: #def2f1;
  color: #000;
}

.wrapper2 {
  max-width: 1170px !important;
  margin: auto;
}

.container {
  width: 100% !important;
  max-width: 100% !important;
}

.pt0 {
  padding-top: 0;
}

.p0 {
  padding: 0;
}

@media screen and (max-width: 480px) {
  .offset-2 {
    margin-left: 0 !important;
  }

  .row.cms-plugin {
    display: block !important;
  }

  .col {
    max-width: 100% !important;
  }

  #masthead {
    background: #fff !important;
  }
}
