    .document .card-body, .cols-document, .cols-4-order-list, .cols-with-cart, .cols-sidebar-right, .cols-small-sidebar-left, .cols-sidebar-left, .cols-2-news, .cols-3-title, .cols-auto-fit,
.cols-3-gallery, .resources .cols-4, .cols-footer, .cols-4, .cols-5, .cols-3-nogap, .cols-3, .cols-2-intro, .cols-2-team, .cols-2, .project-grid {
