}

@supports (grid-area: auto) {
  @media only screen and (min-width: 56.25em) {
    .project-grid {
      grid-template-columns: auto 350px;
    }
  }
  .project-grid .blog-visual {
    grid-column: 1;
    grid-row: 1;
  }
  .project-grid div:nth-child(2) {
    grid-column: 2;
    grid-row: 1;
    border-left: 2px solid #27363b;
    padding-left: 30px;
  }
  .project-grid div:nth-child(2) h4 {
    padding: 20px 0 30px;
    border-bottom: 1px solid #ddd;
    font-size: 16px;
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 0.13em;
    line-height: 16px;
  }
  .project-grid div:nth-child(3) {
    grid-column: 1/3;
