/* Acordeon styles */
.tab,
.map-filters {
  position: relative;
  margin-bottom: 1px;
  width: 100%;
  overflow: hidden;
  /* :checked */
  /* Icon */
}
.tab input,
.map-filters input {
  position: absolute;
  opacity: 0;
  z-index: -1;
}
.tab label,
.map-filters label {
  position: relative;
  color: #212121;
  display: block;
  padding: 0 0 0 1em;
  border-top: 2px solid #212121;
  font-weight: bold;
  line-height: 3;
  cursor: pointer;
}
.tab label:hover,
.map-filters label:hover {
  color: #1d552e;
}
.tab .tab-content,
.map-filters .tab-content {
  max-height: 0;
  overflow: hidden;
  background: transparent;
  -webkit-transition: max-height 0.35s;
  -o-transition: max-height 0.35s;
  transition: max-height 0.35s;
}
.tab .tab-content p,
.map-filters .tab-content p {
  margin: 1em;
}
.tab input:checked ~ .tab-content,
.map-filters input:checked ~ .tab-content {
  max-height: 10em;
}
.tab label::after,
.map-filters label::after {
  position: absolute;
  right: 0;
  top: 0;
  display: block;
  width: 3em;
  height: 3em;
  line-height: 3;
  text-align: center;
}
.tab input[type=checkbox] + label::after,
.map-filters input[type=checkbox] + label::after {
  content: "+";
}
.tab input[type=checkbox]:checked + label::after,
.map-filters input[type=checkbox]:checked + label::after {
  content: "-";
}
.tab input[type=checkbox]:checked + label,
.map-filters input[type=checkbox]:checked + label {
  color: #1d552e;
}

